@import url(https://fonts.googleapis.com/css?family=Lato:300,400&display=swap);
*, *::after, *::before {
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    margin: 0;
    font-family: 'Lato';
    color: #222;
    background-color: #dadada;
}

a span {
    position: relative;
}

a span:after {
    content: "";
    width: 100%;
    height: 2px;
    margin-top: 2px;
    background-color: currentColor;
    display: block;
    position: absolute;
    opacity: 0;
    transform: translate3d(0, -15px, 0);
    transition: 0.4s all ease;
}

a:hover span:after {
    opacity: 0.8;
    transform: translate3d(0, 0, 0);
}

main {
    padding: 60px 0;
    min-height: 700px;
}

.container {
    width: 960px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.centralizar {
    justify-content: center;
    align-items: center;
    display: flex;
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
    margin-left: 10px;
    margin-right: 10px;
}

.col-1 	{width: 60px;}
.col-2 	{width: 140px;}
.col-3 	{width: 220px;}
.col-4 	{width: 300px;}
.col-5 	{width: 380px;}
.col-6 	{width: 460px;}
.col-7 	{width: 540px;}
.col-8 	{width: 620px;}
.col-9 	{width: 700px;}
.col-10 {width: 780px;}
.col-11 {width: 860px;}
.col-12 {width: 940px;}

@media only screen and (max-width: 979px) {
    .container {
        width: 720px;
    }

    .col-1 	{width: 40px;}
    .col-2 	{width: 100px;}
    .col-3 	{width: 160px;}
    .col-4 	{width: 220px;}
    .col-5 	{width: 280px;}
    .col-6 	{width: 340px;}
    .col-7 	{width: 400px;}
    .col-8 	{width: 460px;}
    .col-9 	{width: 520px;}
    .col-10 {width: 580px;}
    .col-11 {width: 640px;}
    .col-12 {width: 700px;}
}

@media only screen and (max-width: 739px) {
    .container {
        width: auto;
        max-width: 360px;
    }

    .col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
        width: 100%;
        margin-bottom: 30px;
        margin-left: 0;
        margin-right: 0;
    }
}
header {
    text-align: center;
    background: #FFF;
    padding: 30px 0px 15px;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.4);
}

header h1 {
    margin: 0;
}

header h1::after {
    content: "";
    width: 100px;
    height: 1px;
    background-color: currentColor;
    display: block;
    margin: 15px auto 0px;
}

ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

header li + li{
    margin-left: 15px;
}

header a {
    text-decoration: none;
    padding: 15px 8px;
    color: inherit;
    font-weight: bold;
    display: block;
}
.buscaContainer {
    margin: 0 auto;
    flex-direction: column;
}

.buscaContainer input {
    padding: 8px 15px;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 4px;
    margin-top: 15px;
    font-size: 16px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
    outline-color: rgba(0, 0, 0, 0.4);
}

.buscaContainer button {
    padding: 8px;
    border: none;
    border-radius: 4px;
    margin-top: 15px;
    font-size: 18px;
    font-weight: bold;
    color: #FFF;
    background-color: rgba(0, 0, 0, 0.8);
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
    cursor: pointer;
    transition: all ease 0.3s;
}

.buscaContainer button:hover {
    background-color: rgba(0, 0, 0, 0.7);
}

@media only screen and (max-width: 979px) {
    .buscaContainer button {
        padding: 16px 0;
    }

    .buscaContainer input {
        padding: 15px;
    }
}

@media only screen and (max-width: 739px) {
    .buscaContainer button {
        margin-bottom: 0;
    }

    .buscaContainer input {
        margin-bottom: 0;
    }
}
.Card {
    padding: 30px;
    border-radius: 8px;
    background-color: #FFF;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.4);
}

.Card h1, .Card h2 {
    margin: 0;
    font-size: 24px;
    text-align: center;
    text-transform: capitalize;
}

.Card .container-img {
    width: 160px;
    height: 160px;
    padding: 20px;
    border-radius: 50%;
    margin: 30px auto;
    box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.4);
    overflow: hidden;
}

.Card .container-img img {
    margin: 0;
    max-width: 100%;
    display: block;
}

@-webkit-keyframes girar {
	from {
		transform: rotate(0);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes girar {
	from {
		transform: rotate(0);
	}
	to {
		transform: rotate(360deg);
	}
}

.Card.carregando .container-img{
	padding: 0;
}

.Card.carregando .container-img img {
	-webkit-animation: girar 1s infinite;
	        animation: girar 1s infinite;
}

.Card p {
    margin: 0;
    font-size: 18px;
    text-align: center;
}

.Card .numero {
    max-width: 60px;
    padding: 5px 10px;
    border-radius: 10px;
    margin: 0 auto 15px;
    font-size: 16px;
    color: #777;
    background-color: rgba(0, 0, 0, 0.1);
}

.Card .tipos {
    color: #777;
    text-align: center;
    font-weight: bold;
    text-transform: capitalize;
}

.Card .fire {color: #F62}
.Card .grass  { color: #6F0 }
.Card .electric  { color: #EA2 }
.Card .water  { color: #20F }
.Card .ground  { color: #BB0 }
.Card .rock  { color: #770 }
.Card .fairy  { color: #F99 }
.Card .poison  { color: #B2F }
.Card .bug  { color: #AC0 }
.Card .dragon  { color: #079 }
.Card .psychic  { color: #D08 }
.Card .flying  { color: #AAC }
.Card .fighting  { color: #B22 }
.Card .normal  { color: #888 }
.Card .ice  { color: #2AD }
.Card .steel  { color: #B6B6B6 }
footer {
    padding: 30px 0;
    text-align: center;
    background-color: #FFF;
    box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.4);
}

footer h1 {
    margin: 0 0 15px;
    font-size: 24px;
}

footer li {
    margin-top: 15px;
    font-size: 18px;
}

footer li a {
    padding: 8px;
    font-size: 16px;
    text-decoration: none;
    color: inherit;
    display: inline-block;
}

footer li > span::after {
    content: "|";
    margin-left: 10px;
    display: inline;
}

@media only screen and (max-width: 979px) {
    footer li {
        margin-top: 30px;
    }

    footer li a {
        padding: 15px 8px;
    }

    footer li > span::after {
        content: "";
        width: 80px;
        height: 2px;
        background-color: currentColor;
        margin: 15px auto 0;
        display: block;
    }
}

@media only screen and (max-width: 739px) {
    footer h1 {
        font-size: 18px;
    }

    footer h1::after {
        margin: 30px auto 15px;
    }

    footer .col-4 {
        width: 50%;
        margin-top: 15px;
        margin-bottom: 15px;
    }
}
.Pokedex {
    padding: 60px;
}

.Pokedex h1 {
    margin: 0 auto 15px;
    text-align: center;
    text-shadow: 2px 2px 2px #222;
    color: #F4F4F4;
}

.Pokedex .lista {
    flex-wrap: wrap;
}

.Pokedex .lista li {
    margin: 15px 15px 0;
}

@media only screen and (max-width: 979px) {
    .Pokedex {
        padding: 30px;
    }
}

@media only screen and (max-width: 739px) {
    .Pokedex {
        padding: 15px;
    }
}
